body {
  background: linear-gradient(to right, black , #860303);
  /* display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly; */
}

.card {
  -webkit-flex: 0 1 24%;
          flex: 0 1 24%;
  background: #3a3a3a;
  border-radius: 2px;
  max-height: 256px;
  max-width: 256px;
  margin: 1rem;
  position: relative;
  box-shadow: 0 3px 6px #030303, 0 3px 6px #030303;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  /*display: flex;
  justify-content: center; */
  overflow: hidden;
  text-align: center;
  background:#290306;
}
.card > .img-container > img {
  border: #ffee00 5px solid;
  width: 100%;
}

.shake{
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: 5s;
          animation-iteration-count: 5s;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@media screen and (min-width: 600px) {
  .card {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      -webkit-justify-content: space-between;
              justify-content: space-between;
  }

  .img-container {
      -webkit-flex-grow: 0;
              flex-grow: 0;
      -webkit-flex-shrink: 1;
              flex-shrink: 1;
      -webkit-flex-basis: calc(24% - 1em);
              flex-basis: calc(24% - 1em);
  }
}


.navbar {
    background: linear-gradient(to right, black , #860303);
    max-height: 15%;
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #a5a5a5;
    font-size: calc(8px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 10px;
    font-family: 'Chakra Petch', sans-serif;
}

.title {
    font-family: 'Lobster', cursive;
    text-decoration: none;
    color:#d6d6d6;
}

#score {
    font-size: 1.2em;
}


@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}

.gameMessage {
    text-align: center;
    font-size: 1.5em;
    font-family: 'Ubuntu', sans-serif;
}

.black {
    color: #303030;
}

.correct {
    color: ivory;
}

.incorrect {
    color: darkred;
    -webkit-text-stroke: 1px black;
}
